.header{
  width: 100%;
  height: 100px;
  display: flex;
  border-bottom: 3px solid #ccc;
  background: #000;
  .logo{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    .logoLink{
      display: flex;
    }
    img{
      height: 100px;
      width: 300px;
    }
  }
  .mainNav{
    width: 70%;
    height: 100%;
    display: flex;
    ul{
      width: 70%;
      height: 100%;
      display: flex;
      justify-content: space-around;
    }
    li{
      list-style-type: none;
      width: 25%;
      a{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        text-decoration: none;
        font-size: 27px;
        color: #ccc;
        transition-duration: 1s;
        font-family: 'Roboto', sans-serif;
      }
      a:hover{
        transform: scale(1.2);
      }
    }
  }
   .learnReact{
     width: 10%;
     height: 100%;
     display: flex;
     align-items: center;
     a{
       text-decoration: none;
       color: red;
       font-size: 27px;
     }
   }
}