.mainContainer{
  width: 100%;
  height: 800px;
  display: flex;
  justify-content: center;
}
.content{
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  .OryxIana{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: rgb(240, 240, 240);
    .titleOryx{
      font-size: 24px;
    }
    .titleIana{
      font-size: 24px;
    }
  }
  .logoOryxIana{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    img{
      width: 100%;
      height: 40%;
    }
  }
}